/**
 * This injects Tailwind's base styles, which is a combination of
 * Normalize.css and some additional base styles.
 *
 * You can see the styles here:
 * https://github.com/tailwindcss/tailwindcss/blob/master/css/preflight.css
 *
 * If using `postcss-import`, you should import this line from it's own file:
 *
 * @import "./tailwind-preflight.css";
 *
 * See: https://github.com/tailwindcss/tailwindcss/issues/53#issuecomment-341413622
 */
@tailwind preflight;
/**
  * Here you would add any of your custom component classes; stuff that you'd
  * want loaded *before* the utilities so that the utilities could still
  * override them.
  *
  * Example:
  *
  * .btn { ... }
  * .form-input { ... }
  *
  * Or if using a preprocessor or `postcss-import`:
  *
  * @import "components/buttons";
  * @import "components/forms";
  */
/* -- CSS variables */
:root {
  --outlineColor: #5db7a5; }

/* -- For screen readers */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

/* -- A subtle focus style for keyboard-input elements */
.text-input:focus {
  outline: 1px solid var(--outlineColor);
  /* Adjust to suit your tastes */ }

/* -- No outline for non-keyboard-inputs elements */
button:focus,
select:focus {
  outline: none; }

html.user-is-tabbing *:focus {
  outline: 2px solid var(--outlineColor) !important;
  /* for non-webkit browsers */
  outline: 5px auto -webkit-focus-ring-color !important; }

/**
  * This injects all of Tailwind's utility classes, generated based on your
  * config file.
  *
  * If using `postcss-import`, you should import this line from it's own file:
  *
  * @import "./tailwind-utilities.css";
  *
  * See: https://github.com/tailwindcss/tailwindcss/issues/53#issuecomment-341413622
  */
@tailwind utilities;
/* -- Fonts */
.fonts-loaded body,
.fonts-loaded h1 {
  font-family: Proxima-Nova-Regular, sans-serif;
  font-style: normal; }

.fonts-loaded strong {
  font-family: Proxima-Nova-Bold, sans-serif; }

.fonts-loaded h2,
.fonts-loaded h3 {
  font-family: Proxima-Nova-Black, sans-serif; }

.fonts-loaded .kontakt__email,
.fonts-loaded .kontakt__phone {
  font-family: Proxima-Nova-Black, sans-serif; }

/* -- Fontello */
.fonts-loaded [class^="icon-"]:before {
  font-family: "fontello"; }

.fonts-loaded [class*=" icon-"]:before {
  font-family: "fontello"; }

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */ }

/**
  * Here you would add any custom utilities you need that don't come out of the
  * box with Tailwind.
  *
  * Example :
  *
  * .bg-pattern-graph-paper { ... }
  * .skew-45 { ... }
  *
  * Or if using a preprocessor or `postcss-import`:
  *
  * @import "utilities/background-patterns";
  * @import "utilities/skew-transforms";
  */
h1 {
  font-size: 1.8rem;
  margin: 0; }

